import React, {Component} from 'react';
import { Tabs, Tab } from '@material-ui/core';
import { withStyles } from '@material-ui/core/styles';
import Login from './Login';
import RebatesCampaignNoInfo from './RebatesCampaign/RebatesCampaignNoInfo'
import RebatesCampaignContent from './RebatesCampaign/RebatesCampaignContent'
import CampaignDisplayList from '../../views/CampaignDisplay';

export default class RakutenTabs extends Component {
    constructor(props) {
        super(props);
        this.state = {
            value: 1,
            campaignInfoExists: false,
        };
        this.onTabSelectionChanged = this.onTabSelectionChanged.bind(this);
    }

    onTabSelectionChanged(event, value) {
        this.setState({ value: value });
        if (value === 2) {
            platform_api_root.runtime.sendMessage({
                method: "google_analytics", value: "notice_tab"
            });

            if (!this.state.campaignInfoExists) {
                platform_api_root.runtime.sendMessage({
                    method: "getRebatesCampaignList"
                }, (response) => {
                    if (response) {
                        const campList = new CampaignDisplayList();
                        campList.generateCampaignDisplayList(response);
                        if (campList.campaigns && campList.campaigns.length > 0) {
                            this.campaignInfo = campList.campaigns;
                            this.setState({ campaignInfoExists: true });
                        }
                    }
                });
            }
        }
        else {
            platform_api_root.runtime.sendMessage({
                method: "google_analytics", value: "mypage_tab"
            });
        }
    }

    render() {
        const styles = {
            serpFrameStyle: {
                 width: "320px",
                 border: "none",
                 margin: "0px",
                 padding: "0px"
            },
            dfpadFrameStyle: {
                 border: "none",
                 width: "300px",
                 height: "0px",		// it will be set dynamically when iframe content is loaded.
                 marginTop: "0px",
                 marginBottom: "0px",
                 marginLeft: "10px",
                 marginRight: "10px",
                 padding: "0px"
            },
        };

        const MyTabs = withStyles({
            root: {
                paddingTop: "0px",
                paddingBottom: "0px",
                backgroundColor: "white",
                minHeight: "0px",
            },
            indicator: {
                backgroundColor: "#00A0F0",
                height: "5px",
            },
        })(Tabs);

        const MyTab = withStyles({
            root: {
                marginTop: "5px",
                marginBottom: "8px",
                paddingTop: "0px",
                paddingBottom: "0px",
                minHeight: "0px",
                height: "20px",
                textAlign: "center",
                fontSize: "12px",
                fontWeight: "bold",
                color: "#333333",
            },
            selected: {
                color: "#00A0F0",
            },
        })(Tab);

        const TabPanelStyleForValue = (value) => {
            let style = {
                width: "100%",
                position: "relative",
            };
            if (this.state.value !== value) {
                style.height = "0px";
                style.overflow = "hidden";
            }
            return style;
        };

        return (
            <div id="tabRoot">
                <MyTabs variant="fullWidth"
                        value={this.state.value}
                        onChange={this.onTabSelectionChanged}>
                    <MyTab label="" value={1}/>
                    <MyTab label="" value={2}/>
                </MyTabs>
                <div style={TabPanelStyleForValue(1)}>
                    <div id="box2content1" className="contentDiv">
                         <div id="iframe_pickup">
                            <iframe seamless frameBorder="0" scrolling="no" id="pickup_content"></iframe>
                        </div>
                        <div id="login_contents">
                            <Login />
                        </div>
                        <div id="iframe_part">
                            <iframe style={styles.serpFrameStyle} seamless frameBorder="0" scrolling="no" src="/img/ic_loading.gif" id="content"></iframe>
                        </div>
                        {/*<div id="iframe_dfpad">
                            <iframe style={styles.dfpadFrameStyle} seamless frameBorder="0" scrolling="no" id="dfpad"></iframe>
                        </div>*/}
                    </div>
                </div>
                <div style={TabPanelStyleForValue(2)}>
                    <div id="box2content2" className="contentDiv">
                        {this.state.campaignInfoExists
                             ? <div id="rebatesCampaign"><RebatesCampaignContent campaigns={this.campaignInfo} /></div>
                             : <RebatesCampaignNoInfo />}
                    </div>
                </div>
            </div>
        );
    }
}

RakutenTabs.propTypes = {
};
