import React, { Component } from 'react';
import PropTypes from 'prop-types';
import Rebates from './Rebates';
import UserInfo from './UserInfo';
import HoverContent from './popuphover/HoverContent';
import  { RebatesInfo } from  '../../../../../utilities/src/rebates_const'
import  { CONST_RAKUTEN_WEBSEARCH, BANNER_DISABLE_OPTION } from  '../../../../../utilities/src/setting_config'
import PopupHelper from '../../../../../utilities/src/popupHelper'

export default class Footer extends Component {

  constructor(props) {
    super(props);
    this.state = {
      rebates_info: this.props.rebates_info == null ? new RebatesInfo() : this.props.rebates_info,
      login: this.props.login,
      activated: this.props.activated,
    };
    this.extensionHelper = new PopupHelper();
  }

  componentDidMount() {
    //footer
    document.getElementById('footer_homepage_link').addEventListener('click', () => {
      platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "homepage_link" });
      this.extensionHelper.openWindowClosePopup(`${CONST_RAKUTEN_WEBSEARCH.defaultUrl}`, 'homeWin');
    });

  }

  render() {
    let styles = {
      divParent :{
        "width":"100%"
      },
      col3 :{
        "width":"30px",
        "float":"left"
      },
      col1 :{
        "width":"145px",
        "float":"left"
      },
      imgStyle: {
        "float":"right"
      }
    }
    return (
      <div id="footer">
        <div style={styles.divParent}>
          <a  style={styles.col1} id="footer_homepage_link" href="#"><img src="img/logo.png" height={20} alt="" /></a>
          {this.state.login?<UserInfo user_pointInfo = {this.props.user_pointInfo}/>:null}
          <div id="user_details" style={styles.col3} className="user_info">
              <HoverContent user_pointInfo = {this.props.user_pointInfo} />
              <img src="img/user_logo.png" style={styles.imgStyle} width={20} height={20} alt="" />
          </div>
        </div>

        <p id="rakuten_not_default">
        </p>
        {
          this.props.banner_disable_option == BANNER_DISABLE_OPTION.NO_USE ? null :
          <Rebates onClick={this.props.onClick} rebates_info={this.state.rebates_info} activated={this.state.activated} />
        }
      </div>
    );
  }
}

Footer.propTypes = {
    banner_disable_option: PropTypes.number,
    rebates_info: PropTypes.object,
    user_pointInfo:PropTypes.object,
    login: PropTypes.bool,
    activated: PropTypes.bool,
    onClick: PropTypes.func,
};