import React, { Component } from 'react';

export default class KuchisuInfo extends Component {
  render() {
    return (

      <div id="kuchiInfo">
        <div id="progressbar">
          <div id="progress" style={{ width: '0%' }} />
        </div>
        <div id="kuchi">
          <span id="curc">0/30</span>
        </div>
      </div>

    );
  }
}