var zoomfactor = 0;
var CHROME_EXT_MAX_HEIGHT = 595;

import React from 'react';
import {render} from 'react-dom';
import {Store} from 'react-chrome-redux';

import KuchisuInfo from './components/app/KuchisuInfo';
import RakutenContents from './components/app/RakutenContents';
import Footer from './components/app/Footer';
import RakutenTabs from './components/app/RakutenTabs';

import  { DFPAD_CONTENT_URL, isActivatedPageAlreadyExpired } from  '../../../utilities/src/rebates_const'
import  { CONST_RAKUTEN_WEBSEARCH } from  '../../../utilities/src/setting_config'
import PopupHelper from '../../../utilities/src/popupHelper'
import  { sendMessageByPort } from  '../../../utilities/src/common_helper'

let extensionHelper = new PopupHelper();
const proxyStore = new Store({ portName: 'background_data', extensionId: null });

//Autofocus searchbox
document.getElementById("searchBox").focus();

proxyStore.subscribe(() => {
	//kuchisu_content
	render(<KuchisuInfo />, document.getElementById('kuchisu_contents'));

	//rakuten_content
	render(<RakutenContents />, document.getElementById('rakuten_contents'));

	//footer
	const data = proxyStore.getState().data;
	const extensionSetting = data.ExtensionSetting;

	//rakuten_content_dynamic
	render(<RakutenTabs />, document.getElementById('box2'));

	const rebates_info = data.rebates_info;
	const rebates_activated_sites = data.rebates_activated_sites;
	let bActivated = false;

	if (rebates_activated_sites != null) {
		for (let v of rebates_activated_sites) {
			if (v.domain == rebates_info.domainRegex && rebates_info.tokenAvailable) {
				if (isActivatedPageAlreadyExpired(v.time) == false) {
					bActivated = true;
				}
				else {
					let activated_data = {
						domain: v.domain,
						is_popup: false,
						time: new Date().getTime(),
					};
					proxyStore.dispatch({
						type: 'REMOVE_EXPIRED_ACTIVATED_SITE',
						data: activated_data
					});
				}

				break;
			}
		}
	}

	render(<Footer onClick={activateCashBack}
                        user_pointInfo = {data.userPointInfo}
                        banner_disable_option = {extensionSetting.BannerDisableOption}
                        affiliate_options = {extensionSetting.AffiliateOption}
                        rebates_info = {rebates_info}
                        activated = {bActivated}
                        login = {data.login == 1}
						/>,
                document.getElementById('footer_contents'));

    viewContent();
});
// message
window.addEventListener("message", recieveMessage, false);

let port = platform_api_root.runtime.connect({name: "callFromPopup"});
port.onMessage.addListener(function(msg) {
	// background returns callback
	if (msg.popup_url != undefined) {
		viewContent(msg.popup_url);
		sendMessageByPort(port, "CallPageZoom");
	}
	else if(msg.ZoomFactor != undefined){
		zoomfactor = msg.ZoomFactor;
		sendMessageByPort(port, "RefreshRebatesCampaign");
	}
	else if(msg.RebatesCampaign != undefined){
			// No handler yet.
	}
    // iframe is not accepted in V3
	// else if (msg.pickup_data_page != undefined) {
	// 	viewPickupContent(msg.pickup_data_page);
	// }
});
sendMessageByPort(port, "CallSubWindow");

// Ensure the popup is closed when a new tab/window is opened
// due a click inside of the dfpad element: it appears hard to
// handle clicks directly in dfpad content due multiple internal
// iframes from different domains, so a "simple" solution
// is used - when a link is clicked it opens a new tab/window
// which becomes focused/activated which we catch and close the popup.
platform_api_root.tabs.onActivated.addListener(() => {
	port.disconnect();
	window.close();
});

function activateCashBack(e){
	let rebates_activated_sites = proxyStore.getState().data.rebates_activated_sites;
	let rebates_info = proxyStore.getState().data.rebates_info;
	if (rebates_activated_sites.findIndex((item) => {
		return item.domain == rebates_info.domainRegex;
	}) == -1) {
		let activated_data = {
			domain: rebates_info.domainRegex,
			is_popup: false,
			time: new Date().getTime(),
		};
		proxyStore.dispatch({
			type: 'ADD_ACTIVATED_SITE',
			data: activated_data
		});
	}
	proxyStore.dispatch({
		type: 'REMOVE_REBATES_POPUP_CLOSE_SITE',
		data: rebates_info.domainRegex
	});
}
function searchweb(e) {
	const searchBoxValue = document.getElementById("searchBox").value;

	let suggestSelection = "0";
    const suggestInputElem = document.querySelector("input[name=sg]");
    if (suggestInputElem) {
        suggestSelection = suggestInputElem.value;
    }

	let historySelection = "0";
    const historyInputElem = document.querySelector("input[name=hs]");
    if (historyInputElem) {
        historySelection = historyInputElem.value;
    }

	let url = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/Web?qt=${encodeURIComponent(searchBoxValue)}&ref=platform_specific_ext_ref&tool_id=1`;
	if (suggestSelection === "1") {
		url = `${url}&sg=${encodeURIComponent(suggestSelection)}`;
	}
	if (historySelection === "1") {
		url = `${url}&hs=${encodeURIComponent(historySelection)}`;
	}
	platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "websearch_button" });
	extensionHelper.openWindowClosePopup(url, '_blank');
}

// ichiba search button
function searchIchiba() {
	const searchBoxValue = document.getElementById("searchBox").value;
	const url = 'https://search.rakuten.co.jp/search/mall/' + encodeURIComponent(searchBoxValue) + '/-/?scid=tb_r_crm_icf';
	platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "productsearch_button" });
	extensionHelper.openWindowClosePopup(url, '_blank');
}

// warning!! since extension cannot handle inline event in html, we need to bring all events here...
//event handler
document.getElementById('ichibaSearchBtn').addEventListener('click', searchIchiba);
document.getElementById('websearchBtn').addEventListener('click', searchweb);

// message
function recieveMessage(e) {
	if (e.data == "close") {
		port.disconnect();
		window.close();
	}
	else if (e.data && e.data.msg === 'DFpadContentReady') {
		if (e.data.height > 0) {
			var dfpad = document.getElementById('dfpad');
			dfpad.style.height = e.data.height + 'px';
			dfpad.style.marginTop = dfpad.style.marginBottom = '5px';
		}
	}
	else {
		receiveSize(e);
	}

}
function isPageZoomed() {
	if (zoomfactor && (zoomfactor != 1 && zoomfactor > 0)) {
		return true;
	}
	return false;
}
// iframe
function receiveSize(e) {
	let urlMatch = /^(http|https):\/\/websearch.rakuten.co.jp/i;
	if (urlMatch.test(e.origin)) { // for security: set this to the domain of the iframe - use e.uri if you need more specificity
		if (e.data) {
			var scrollHeight = e.data;
			if (isPageZoomed()) {
				scrollHeight = (Math.round((scrollHeight * zoomfactor)));
			}
			setScrollHeight(scrollHeight);
		}
	}
}

function setScrollHeight(height) {
	document.getElementById("content").style.height = height + "px";
}

// iframesrc
function viewContent(url){
        if (url) {
		if (proxyStore.getState().data.URLCheck == "NG") {
			document.getElementById("content").setAttribute("src", "maintenance.html");
		} else {
			document.getElementById("content").setAttribute("src", url);
		}

		document.getElementById("content").onload = () => {
			const dfpad = document.getElementById("dfpad");
			if (dfpad != null && !dfpad.src) {
				dfpad.src = `${DFPAD_CONTENT_URL}`;
            }
		};
	}

	if (proxyStore.getState().data.kuchisuCount != undefined) {
		var kuchisu = proxyStore.getState().data.kuchisuCount;
		var pos1 = kuchisu.indexOf('/');
		var num_part = kuchisu.substr(0, kuchisu.indexOf(''));
		var numerator = Number(num_part.substr(0, pos1));
		var denominator = Number(num_part.substr(pos1 + 1));
		var parcent = (numerator / denominator) * 100;

		document.getElementById("curc").innerHTML = kuchisu;
		document.getElementById("progress").style.width = parcent.toString() + "%";
	}

	if (proxyStore.getState().data.yamawakeInfo != undefined) {
		document.getElementById("searchBox").setAttribute("placeholder", proxyStore.getState().data.yamawakeInfo);
	}

	if (proxyStore.getState().data.login == 0) {
		//document.getElementById("footer_logout_link").innerHTML = "";
		document.getElementById("login_screen").style.display = "block";
		document.getElementById("kuchiInfo").style.display = "none";
	} else {
	//	document.getElementById("footer_logout_link").innerHTML = "";
		document.getElementById("login_screen").style.display = "none";
		document.getElementById("kuchiInfo").style.display = "block";
	}

	if (proxyStore.getState().data.isRakutenAsDefault == false) {
		document.getElementById("rakuten_not_default").innerHTML = `<a id='rakuten_not_default_link'
			href='${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/static/platform_identifier/welcome.html?scid=platform_welcomeparam' target='_blank'><br></a>`;
	} else {
		document.getElementById("rakuten_not_default").innerHTML = "";
	}

        renderPopUpContent();
}

