import React, { Component } from 'react';
import PropTypes from 'prop-types';
import  { CONST_RAKUTEN_WEBSEARCH } from  '../../../../../../utilities/src/setting_config'
import FooterLink from './FooterLink'
import PointInfo from './PointInfo'
import {EXTENSION_FAQ_LINK, EXTENSION_POPUP_TANDC_LINK} from '../../../../../../utilities/src/common_const'

export default class HoverFooter extends Component {
    constructor(props) {
        super(props);
        this.state = {login: this.props.login};
        this.login_url = "";
        this.logout_url = "";
    }

    componentDidMount() {
        this.login_url = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/rtoken_login.html?tool_id=1`;
        this.logout_url = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/rtoken_logout.html?tool_id=1`;
    }

  render(){
       let styles = {
            divStyle: {verticalAlign: "top"}
        }

      return(
            <div id="footer_contents" style={styles.divStyle}>
                <ul className="footer_text_link">
                    <PointInfo />
                    <FooterLink lnkId="footer_setting_link" dispText="" lnkUrl="/options.html" googleAnalyticsText="setting_link" />
                    <FooterLink lnkId="footer_tandc_link" dispText="" lnkUrl={EXTENSION_POPUP_TANDC_LINK} googleAnalyticsText="tandc_link" position="right" />
                    <FooterLink lnkId="footer_help_link" dispText="" lnkUrl={EXTENSION_FAQ_LINK} googleAnalyticsText="FAQ_link" />
                    {this.state.login?
                        <FooterLink lnkId="footer_logout_link" dispText="" lnkUrl={this.logout_url} position="right" googleAnalyticsText="logout_link" login={this.state.login} />
                    :
                        <FooterLink lnkId="footer_login_link" dispText="" lnkUrl={this.login_url} position="right" googleAnalyticsText="login_link" login={this.state.login} />
                    }
                </ul>
            </div>
      );
  }
}

HoverFooter.propTypes={
    login:PropTypes.bool,
}