import React, { Component } from 'react';
import PropTypes from 'prop-types';
import UserPointInfo from './UserPointInfo';
import HoverFooter from './HoverFooter';


export default class HoverContent extends Component {
    constructor(props) {
        super(props);
        this.state = {
            loggedin:this.props.user_pointInfo!=null?this.props.user_pointInfo.name != '':false
        }
    }

    render(){
        return(
            <div id="userinfo_popup">
            {this.state.loggedin?<UserPointInfo user_pointInfo={this.props.user_pointInfo}/>:null}
            <HoverFooter login={this.state.loggedin} />
          </div>);
    }
}

HoverContent.propTypes={
    user_pointInfo:PropTypes.object,
}