import React, { Component } from 'react';
import PopupHelper from '../../../../../utilities/src/popupHelper'
import  { CONST_RAKUTEN_WEBSEARCH } from  '../../../../../utilities/src/setting_config'

export default class Login extends Component {
  constructor() {
    super();
    this.extensionHelper = new PopupHelper();
  }

  componentDidMount() {
    this.login_url = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/rtoken_login.html?tool_id=1`;
    document.getElementById('loginMsg').addEventListener('click', () => {
      platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "login_link" });
      this.extensionHelper.openWindowClosePopup(this.login_url, 'tbChromeWindow');
    });

    document.getElementById('login').addEventListener('click', () => {
      platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "login_button" });
      this.extensionHelper.openWindowClosePopup(this.login_url, 'tbChromeWindow');
    });
  }

  render() {
    return (

      <div className="login" id="login_screen">
        <div id="loginMsg">
          
          <a href="#"></a> 
        </div>
        <div id="login">
          <a href="#" className="myButton">  </a>
        </div>
      </div>

    );
  }
}

Login.propTypes = {
};