import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PopupHelper from '../../../../../../utilities/src/popupHelper'

export default class PointDetails extends Component {
    constructor(props) {
        super(props);
        this.extensionHelper = new PopupHelper();
        this.rakutenRankStyle = '';
        this.rakutenMemberType = '';
        this.initialize();
    }


    initialize(){
       this.initRakutenMemberStyle();
    }

    initRakutenMemberStyle(){
        switch(this.props.user_pointInfo.rank){
            case "2":
                this.rakutenRankStyle = 'rakutenSilver rakutenRank';
                this.rakutenMemberType = '';
                break;
            case "3":
                 this.rakutenRankStyle = 'rakutenGold rakutenRank';
                 this.rakutenMemberType = '';
                break;
            case "4":
                this.rakutenRankStyle = 'rakutenPlatinum rakutenRank';
                this.rakutenMemberType = '';
                break;
            case "5":
                this.rakutenRankStyle = 'rakutenDiamond rakutenRank';
                this.rakutenMemberType = '';
                break;
            default:
                this.rakutenRankStyle = 'rakutenRegular rakutenRank';
                this.rakutenMemberType = '';
                break;
        }
    }

     componentDidMount() {
        document.getElementById("lnkMyRakutenPoint2").addEventListener("click", ()=>{
            platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "pointclub2_link" });
            this.extensionHelper.openWindowClosePopup(`https://point.rakuten.co.jp/?scid=tb_r_crm_pointclub`, 'pointclub2_link');
        });
    }

      render(){
        return(
             <div className={this.rakutenRankStyle}>
                <div className="rakutenRankFrame">
                    <div className="rakutenYourRank">
                        <span className="rakutenYourRankFrame">
                            <span className="rakutenRanker"><img
                                src="https://image.infoseek.rakuten.co.jp/content/toolbar-platform/websearch/image/searchresult/t.gif"
                                alt=""/></span>
                                <span className="rakutenYourRankCase">
                                    <span className="rakutenRankName"><span></span><a id="lnkMyRakutenPoint2" href="#">
                                    {this.rakutenMemberType}</a></span>
                                </span>
                        </span>
                    </div>
                    <dl className="rakutenYourPoint">
                        <dt></dt>

                        <dd>
                            <span className="rakutenPointNumber">{this.props.user_pointInfo.totalPoint}</span><span
                                className="rakutenPointUnit"></span>
                        </dd>
                    </dl>
                    <div className="rakutenExtraPoint">
                        <div className="rakutenExtraPointer">
                            <img
                                src="https://image.infoseek.rakuten.co.jp/content/toolbar-platform/websearch/image/searchresult/t.gif"
                                alt=""/>
                        </div>
                        <ul>
                            <li><dl className="rakutenYourLimited">
                                    <dt></dt>
                                    <dd>
                                        {this.props.user_pointInfo.termPoint}
                                        
                                    </dd>
                                </dl>{this.props.user_pointInfo.cash != '0' ? '|':'' }</li>
                                {this.props.user_pointInfo.cash != '0' ? (
                                    <li><dl className="rakutenYourCash">
                                    <dt></dt>
                                    <dd>
                                        {this.props.user_pointInfo.cash}
                                        
                                    </dd>
                                    </dl></li>):''
                                }

                        </ul>
                    </div>
                          <dl className="rakutenYourPoint">
                    <dt></dt>
                    <dd>
                        <span className="rakutenPointNumber">{this.props.user_pointInfo.futureGrantedPoint}</span>
                        <span className="rakutenPointUnit"></span>
                    </dd>
                </dl>
                </div>

            </div>
        );
      }
}
PointDetails.propTypes={
    user_pointInfo:PropTypes.object
}