import React, { Component } from 'react';
import  { CONST_RAKUTEN_WEBSEARCH } from  '../../../../../utilities/src/setting_config'
import  PopupHelper from  '../../../../../utilities/src/popupHelper'

export default class RakutenContents extends Component {

  constructor() {
    super();
    this.extensionHelper = new PopupHelper();
  }

  componentDidMount() {
    document.getElementById('ichiba_link').addEventListener('click', () => { this.clickDoublePointLink('ichiba') });
    document.getElementById('cart_link').addEventListener('click', () => { this.clickDoublePointLink('kago') });
    document.getElementById('coupon_link').addEventListener('click', () => { this.clickDoublePointLink('coupon') });
    document.getElementById('view_history_link').addEventListener('click', () =>{ this.clickDoublePointLink('view_history') });
    document.getElementById('bookmark_link').addEventListener('click', () => { this.clickDoublePointLink('bookmark') });
    document.getElementById('purchase_history_link').addEventListener('click', () => { this.clickDoublePointLink('purchase_history') });
  }

  /*
 * Double Point related function
 */
  clickDoublePointLink(category) {

    //
    let link = undefined;
    switch (category) {
      case "ichiba":
        link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=ichiba&platform=unifiedext`;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "ichiba" });
        break;
      case "kago":
        link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=kago&platform=unifiedext`;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "shoppingcart_button" });
        break;
      case "coupon":
        link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=coupon&platform=unifiedext`;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "coupon_button" });
        break;
      case "bookmark":
        link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=bookmark&platform=unifiedext`;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "bookmark_button" });
        break;
      case "purchase_history":
        link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=purchase_history&platform=unifiedext`;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "purchasehistory_button" });
        break;
      case "view_history":
        link = `${CONST_RAKUTEN_WEBSEARCH.defaultUrl}/dpr?btnType=view_history&platform=unifiedext`;
        platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "browsinghistory_button" });
        break;
    }
    this.extensionHelper.openWindowClosePopup(link, '');
  }

  render() {
    return (

      <div id="ichibaContent">
        <ul>
          <li><a title="" id="ichiba_link" href="#"><span id="ichibaTop" className="icon" /></a></li>
          <li className="small"><a title="" id="cart_link" href="#"><span id="cart" className="icon" /></a></li>
          <li className="coupon"><a title="my" id="coupon_link" href="#"><span id="coupon" className="icon" /></a></li>
          <li className="small"><a title="" id="view_history_link" href="#"><span id="ashiato" className="icon" /></a></li>
          <li className="small"><a title="" id="bookmark_link" href="#"><span id="bookmark" className="icon" /></a></li>
          <li><a title="" id="purchase_history_link" href="#"><span id="order" className="icon" /></a></li>
        </ul>
      </div>

    );
  }

}
