import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PopupHelper from '../../../../../utilities/src/popupHelper'

export default class UserInfo extends Component {
    constructor(props) {
        super(props);
        this.extensionHelper = new PopupHelper();
    }

     componentDidMount(){
        document.getElementById("lnkMyRakuten").addEventListener("click", ()=>{
            platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "myrakuten_link" });
            this.extensionHelper.openWindowClosePopup(`https://my.rakuten.co.jp/?scid=tb_r_crm_myrakuten`, 'myrakuten_link');
        });
        document.getElementById("lnkMyPoint").addEventListener("click", ()=>{
            platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "pointclub_link" });
            this.extensionHelper.openWindowClosePopup(`https://point.rakuten.co.jp/?scid=tb_r_crm_pointclub`, 'pointclub_link');
        });
     }

    render(){
         const styles = {
            divStyle: {
                'float': 'left',
                'width': '135px',
                'borderLeft':'1px solid #bbb',
                'textAlign' : 'right'
            }
        }
        return(
            <div style={styles.divStyle}>
                <span><a id="lnkMyRakuten" href="#">{this.extensionHelper.shortenText(this.props.user_pointInfo.name, 12)} </a></span><br />
                <span><a id="lnkMyPoint" href="#">({this.props.user_pointInfo.totalPoint}&nbsp;)</a></span>
            </div>
        );
    }
}

UserInfo.propTypes = {
  user_pointInfo: PropTypes.object
};