import React, { Component } from 'react';
import PopupHelper from '../../../../../../utilities/src/popupHelper'

export default class PointInfo extends Component {
    constructor(props) {
        super(props);
        this.extensionHelper = new PopupHelper();
    }

     componentDidMount(){
        document.getElementById("lnkMyRakutenPoint").addEventListener("click", (e)=>{
            platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "pointclub_link" });
            this.extensionHelper.openWindowClosePopup(`https://point.rakuten.co.jp/?scid=tb_r_crm_pointclub`, 'pointclub_link');
        });
        document.getElementById("lnkMyRakutenPointHistory").addEventListener("click", ()=>{
            platform_api_root.runtime.sendMessage({ method: "google_analytics", value: "pointhistory_link" });
            this.extensionHelper.openWindowClosePopup(`https://point.rakuten.co.jp/history/?scid=tb_r_crm_pointclub`, 'pointhistory_link');
        });
     }

    render(){
        return(
            <li className="footer_text_link_top">
                <span><a id="lnkMyRakutenPoint" href="#">PointClub</a></span>
                <a id="lnkMyRakutenPointHistory" href="#">()</a>
            </li>
        );
    }
}