import React, { Component } from 'react';
import PropTypes from 'prop-types';
import PopupHelper from '../../../../../../utilities/src/popupHelper'

export default class FooterLink extends Component {
    constructor(props) {
        super(props);
        this.extensionHelper = new PopupHelper();
    }

    componentDidMount() {
        this.liElement.addEventListener("click", () => {
            platform_api_root.runtime.sendMessage({ method: "google_analytics", value: this.props.googleAnalyticsText });
            this.extensionHelper.openWindowClosePopup(this.props.lnkUrl, this.props.googleAnalyticsText);
        });
    }

    render() {
        const liStyle = {};
        if (this.props.position === "right" || this.props.position === "row") {
            liStyle.borderRight = "none";

            if (this.props.position === "row") {
                liStyle.width = "100%";
            }
        }

        return(
            <li style={liStyle}><a id={this.props.lnkId} href="#" ref={c => this.liElement = c}>{this.props.dispText}</a></li>
        );
    }
}

FooterLink.propTypes={
    lnkId:PropTypes.string,
    googleAnalyticsText:PropTypes.string,
    position:PropTypes.string,
    lnkUrl:PropTypes.string,
    dispText:PropTypes.string
}